        <Text v-if="!active || isMobile" class="portfolio-tab-item__header__text">{{ item.tab.text }}</Text>
      </div>
      <Accordion>
        <PortfolioTabItemBody
          v-show="active"
          :item="item"
          :active="active"
        />
      </Accordion>
    </div>
  </div>
